// main scss file

// vendor
@import 'vendor/ionicons';
@import 'vendor/bootstrap';

// General
.container {
  width: map-get($container-max-widths, sm);
}
body {
  font-weight: 300;
}
p {
  margin-bottom: .8em;
}
h1, .h1 {
  color: $brand-primary;
  font-weight: bold;
  line-height: 1.08;
  margin-bottom: 1.5rem;
}
h2, .h2 {
  font-weight: bold;
  line-height: 1.25;
  margin: 1.625rem 0 1.0625rem;
  div {
    font-size: 1.25rem;
    font-weight: 300;
    margin-top: 0.125rem;
  }
}
h3, .h3,
h4, .h4 {
  color: $gray;
  font-weight: 500;
}
h3, .h3 {
  line-height: 1.5;
  margin: 1.3em 0 .9em;
}
h4, .h4 {
  line-height: 1.88;
  margin: .75rem 0 0.125rem;
}
h6, .h6 {
  line-height: 1.33;
}
hr {
  margin: 2rem 0 2.125rem;
  h1 + & {
    margin-top: 2.5rem;
  }
}
abbr[title],
abbr[data-original-title] {
  border-bottom: {
    width: 0.0625rem;
    style: solid;
  }
  text-decoration: none;
}
dt {
  color: $gray;
  font-weight: 500;
}
dd {
  margin-bottom: 1.0625rem;
}
dl.dl-inline {
  align-items: baseline;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: -1rem;
  dt {
    flex-basis: 7.5rem;
    font-size: 1rem;
    line-height: 1.88;
    margin: 0 1.25rem 1rem 0;
  }
  dd {
    flex-basis: 35rem;
  }
}
a {
  &.text-muted {
    color: $text-muted;
    @include hover-focus {
      color: $body-color;
    }
  }
  .ion-ios-arrow-back {
    margin-right: .25em;
    font-size: 1.125rem;
    line-height: .875rem;
    vertical-align: -15%;
  }
  .ion-ios-arrow-forward {
    margin-left: .5em;
    &:before {
      line-height: .875rem;
      font-size: 1.375rem;
      vertical-align: -23%;
    }
  }
}
a.nav-back {
  display: block;
  margin-bottom: 1.5rem;
  @extend .small;
  @extend .text-muted;
}
.text-dark {
  color: $body-color;
}

// Multiline ellipsis
@mixin multiline-ellipsis($line-height, $max-lines, $bg-color: $body-bg) {
  line-height: $line-height;
  max-height: $line-height * $max-lines;
  overflow: hidden;
  padding-right: 1em;
  position: relative;
  &:before {
    bottom: 0;
    content: '…';
    position: absolute;
    right: 0;
  }
  &:after {
    content: '';
    position: absolute;
    right: 0;
    width: 1em;
    height: 1em;
    margin-top: 0.2em;
    background: $bg-color;
  }
}

// Buttons
.btn {
  font-size: .875rem;
  letter-spacing: .0625rem;
  line-height: normal;
  text-transform: uppercase;
}
// Buttons outlined in gray
@mixin button-outline-faded($color) {
  border-color: $gray-lighter;
  &.disabled,
  &:disabled,
  fieldset[disabled] & {
    &:focus,
    &.focus {
      border-color: $gray-lighter;
    }
    @include hover {
      border-color: $gray-lighter;
    }
  }
}
.btn-primary-outline-faded {
  @extend .btn-primary-outline;
  @include button-outline-faded($btn-primary-bg);
}
.btn-secondary-outline-faded {
  @extend .btn-secondary-outline;
  @include button-outline-faded($btn-secondary-border);
}
.btn-info-outline-faded {
  @extend .btn-info-outline;
  @include button-outline-faded($btn-info-bg);
}
.btn-success-outline-faded {
  @extend .btn-success-outline;
  @include button-outline-faded($btn-success-bg);
}
.btn-warning-outline-faded {
  @extend .btn-warning-outline;
  @include button-outline-faded($btn-warning-bg);
}
.btn-danger-outline-faded {
  @extend .btn-danger-outline;
  @include button-outline-faded($btn-danger-bg);
}

// Forms
form {
  background: $gray-lightest;
  border: $border-width solid $gray-lighter;
  border-radius: $border-radius;
  margin-bottom: 2.75rem;
  padding: 2.6875rem 3.6875rem;
  h1 + &,
  .h1 + & {
    margin-top: 1.875rem;
  }
  h1, .h1 {
    line-height: 1.08;
    margin-bottom: 0.9375rem;
    + .form-group {
      margin-top: 1.375rem;
    }
  }
  &.form-sm {
    padding: 1.375rem 1.875rem;
  }
  .btn {
    margin-top: .5rem;
  }
}
label {
  color: $gray;
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.88;
  margin-bottom: .125rem;
}
.form-control {
  line-height: 1.88;
  padding: 0.5625rem $input-padding-x 0.4375rem;
}
select.form-control {
  -webkit-appearance: none;
  .select-container & {
    padding-right: 3rem;
  }
}
.select-container {
  position: relative;
  &:after {
    @extend .ionicons;
    bottom: 0;
    content: "\f3d0"; // ion-ios-arrow-down
    font-size: 1.125rem;
    font-weight: bold;
    line-height: 3rem;
    pointer-events: none;
    position: absolute;
    right: 1.3125rem;
    top: 0;
  }
}
.form-justified {
  align-items: baseline;
  display: flex;
  justify-content: space-between;
  > * + * {
    margin-left: 1.25rem;
  }
  &.form-justified-equal > * {
    flex-basis: 0;
    flex-grow: 1;
  }
  .form-control + .btn,
  .select-container + .btn {
    align-self: center;
    margin: 0 0 0 0.625rem;
    padding: {
      bottom: 0.9375rem;
      top: 0.9375rem;
    }
  }
}
textarea {
  min-height: 7.5rem;
}
.checkbox {
  label {
    padding-left: 0;
    &:before {
      @extend .ionicons;
      background: transparent;
      border: $border-width solid $gray-lightish;
      border-radius: $border-radius;
      color: transparent;
      content: "\00a0";
      font-size: 2rem;
      font-weight: bold;
      line-height: 1.375rem;
      margin: 0.125rem 0.9375rem 0.25rem 0;
      text-align: center;
      @include transition(all .2s);
      width: 1.5rem;
      vertical-align: bottom;
    }
  }
  input[type="checkbox"] {
    position: absolute;
    z-index: -1;
    opacity: 0;
    &:checked + label:before {
      color: #fff;
      content: "\f3ff";
      background: $brand-success;
      border-color: $brand-success;
    }
    &:focus + label:before {
      border-color: $input-border-focus;
    }
  }
}
.captcha-placeholder {
  background: $gray-navbar;
  border-radius: $border-radius;
  border: $border-width solid $gray-lightish;
  color: #a0aab4;
  font-size: 1rem;
  line-height: 1.88;
  padding: 1.5rem 1.25rem;
}

// Form validation
.required label:after {
  content: " *";
}
.missing-required {
  ::placeholder {
    color: $brand-danger;
  }
  &.required label:after {
    color: $brand-danger;
  }
}
.validation-message {
  align-items: center;
  justify-content: flex-end;
  > * {
    margin-top: .5rem;
  }
}

// Navbar
.navbar {
  box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.05);
  font-family: $font-family-serif;
  height: 6.875rem;
  z-index: 1;
  .btn {
    font-size: 1rem;
    font-weight: normal;
    letter-spacing: normal;
    line-height: 1.5;
    margin: 2.0625rem 0 2.0625rem 1.5rem;
    padding: {
      bottom: 0.5625rem;
      top: 0.5625rem;
    }
    text-transform: none;
  }
  .ion-md-search {
    font-size: 1.5rem;
  }
}
.navbar-brand {
  border-right: 0.0625rem solid $gray-navbar;
  margin-right: 1.5rem;
  padding-right: 1.375rem;
  .members & {
    padding-right: 1.875rem;
  }
  img {
    height: 6rem;
    margin: 0.1875rem 0;
  }
}
.navbar-primary {
  font-weight: bold;
}
.navbar-nav {
  .nav-link {
    line-height: 6.875rem;
    padding: 0;
    + .nav-link {
      margin-left: 1.5rem;
    }
  }
  .dropdown .ion-ios-arrow-down {
    margin-left: .25rem;
    &:before {
      font-weight: bold;
    }
  }
}
.nav-item + .nav-item {
  .navbar-nav & {
    margin-left: 1.5rem;
  }
  .nav-pills & {
    margin: 0;
  }
}
.navbar-secondary .nav-item {
  border-right: $border-width solid $gray-navbar;
  padding-right: 1.5rem;
  &:last-child {
    border-right: none;
    padding-right: 0;
  }
}

// Banner
.banner {
  &,
  > * {
    position: relative;
  }
  h1, .h1, h2, .h2, p {
    color: #fff;
    width: 30rem;
  }
  h1, .h1, h2, .h2 {
    margin-top: 7.625rem;
    text-shadow: 0 .125rem 1.5rem rgba(0, 0, 0, 0.4);
  }
  h1, .h1 {
    font-size: 2.625rem;
    line-height: 1.14;
    margin-bottom: 1.25rem;
  }
  h2, .h2 {
    font-size: 2.25rem;
    line-height: 1.44;
    margin-bottom: 1rem;
  }
  p {
    font-size: 1.125rem;
    text-shadow: 0 0 1.5rem rgba(0, 0, 0, 0.6);
  }
  .btn {
    margin: {
      top: 1.45rem;
      bottom: .9rem;
    }
    padding: {
      top: 1rem;
      bottom: 1rem;
    }
  }
  .home & {
    padding: 0 0 1.6rem;
    h1, .h1 {
      margin: 3.25rem 0 1.375rem;
    }
  }
}
// Banner image & overlay
.banner-image {
  background: {
    position: center;
    size: cover;
  }
  left: 0;
  height: 100%;
  min-width: 85.625rem;
  position: absolute;
  top: 0;
  width: 100%;
  &:before {
    background: linear-gradient(to right, rgba(0,0,0,.6), rgba(0,0,0,0));
    content: "";
    position: absolute;
    height: 100%;
    left: 0;
    top: 0;
    width: 67%;
  }
  .home &:before {
    background: linear-gradient(to right, rgba(0,0,0,.82), rgba(0,0,0,0));
  }
}

// Breadcrumbs
.breadcrumb-container {
  background: rgba(243, 244, 245, 0.94);
}
.breadcrumb {
  font-weight: 500;
  font-size: .75rem;
  letter-spacing: 0.0625rem;
  line-height: 1.1666666667;
  margin: 0;
  text-transform: uppercase;
  &, a {
    color: #91969b;
  }
  a {
    @include hover-focus {
      color: $body-color;
    }
  }
  > li + li:before {
    padding-right: .2rem;
    padding-left: .2rem;
  }
}

// Dropdowns
.dropdown-menu {
  display: block;
  font-family: $font-family-sans-serif;
  font-size: .875rem;
  margin: -1.25rem 0 0 -1.3125rem;
  opacity: 0;
  padding: .125rem 0;
  pointer-events: none;
  @include box-shadow(0 .5rem .75rem 0 rgba(0, 0, 0, 0.18));
  @include transition(opacity .2s);
  .open > & {
    opacity: 1;
    pointer-events: inherit;
  }
}
.dropdown-item {
  padding: .625rem 1.25rem;
  + .dropdown-item {
    border-top: .0625rem solid $gray-lightest;
  }
}

// Sidebar
.sidebar {
  padding: {
    bottom: 2rem;
    right: 5.625rem;
    top: .5625rem;
  }
  position: relative;
  &:before {
    background: $gray-navbar;
    bottom: 0;
    content: "";
    position: absolute;
    right: 3.75rem;
    top: 0;
    width: .125rem;
  }
  h3, .h3 {
    margin: 0 0 0.875rem;
  }
  .nav {
    font-weight: normal;
    > nav {
      margin-left: 1.25rem;
      font-style: italic;
    }
  }
  .nav-link {
    color: #a0aab4;
    @include hover-focus {
      color: $body-color;
    }
  }
}

// Main content
.main-container {
  margin: {
    top: 4.625rem;
    bottom: 7.5rem;
  }
  .no-sidebar & {
    padding: {
      left: 0;
      right: 0;
    }
    width: 43.75rem;
  }
  .login & {
    width: 37.5rem;
  }
}
.main {
  font-size: 1.25rem;
  li {
    margin-bottom: .8em;
  }
  ul {
    list-style: none;
    padding-left: 2.5rem;
    > li {
      position: relative;
      &:before {
        color: $brand-primary;
        content: "•";
        left: -1.25rem;
        position: absolute;
      }
    }
  }
  small, .small {
    font-size: 0.875rem;
  }
  strong {
    font-weight: 500;
  }
}

// Profile thumbnails
.profile {
  align-items: center;
  display: flex;
  margin: 1.5rem 0 1rem;
  hr + &,
  + hr {
    margin-top: 2.5rem;
  }
  .profile-thumbnail {
    img {
      border-radius: 50%;
      height: auto;
      margin-right: 1.125rem;
      width: 8.75rem;
    }
  }
  .profile-info {
    font-size: 1rem;
  }
  h3, .h3 {
    margin: 0 0 .8rem;
  }
}

// Asides
.aside {
  background: $gray-lightest;
  border: 1px solid #c6cace;
  border-radius: $border-radius;
  font-size: 0.875rem;
  margin-bottom: 1.25rem;
  padding: 1.625rem 1.5rem;
  width: 13.4375rem;
  &.pull-left {
    margin-right: 2.5rem;
  }
  &.pull-right {
    margin-left: 2.5rem;
  }
  h6, .h6 {
    font-family: $font-family-serif;
    font-size: 1.125rem;
    font-weight: bold;
    margin-bottom: 0.5625rem;
  }
  a {
    font-weight: normal;
  }
}

// Boxes
.box {
  background: $gray-lightest;
  border: .0625rem solid $gray-lighter;
  border-left: .125rem solid $body-color;
  border-radius: $border-radius;
  color: $gray-light;
  display: block;
  font-size: 0.875rem;
  line-height: 1.43;
  margin-bottom: 0.625rem;
  padding: 0.9375rem 1.25rem;
  h6, .h6 {
    color: $body-color;
    line-height: 1.38;
    margin: 0;
  }
  p {
    line-height: 1.29;
    margin: .25rem 0 0;
    + footer {
      margin-top: 0.375rem;
    }
  }
  + hr {
    margin-top: 2.5rem;
  }
  + h2, + .h2 {
    margin-top: 2.125rem;
  }
  footer {
    color: $gray-dark;
    margin-top: .25rem;
    span {
      + span:before {
        content: " • ";
        margin: 0 .25rem;
      }
    }
  }
}
a.box {
  p {
    &:after {
      @extend .ionicons;
      content: "\f3d1"; // ion-ios-arrow-forward
      font-size: 1.125rem;
      line-height: 1.12875rem;
      margin-left: .5rem;
      vertical-align: -10%;
    }
    .text-dark {
      margin-right: -.25rem;
    }
  }
  @include hover-focus {
    border-color: $gray-lightish $gray-lightish $gray-lightish $brand-primary;
    color: $gray-light;
    p:after {
      color: $gray-dark;
    }
  }
}
.box-inline {
  align-items: baseline;
  display: flex;
  h6 {
    flex-basis: 13.75rem;
    margin: 0 1.25rem -0.0625rem 0;
  }
  p {
    flex-basis: 26.25rem;
    margin: 0;
  }
}
.box-container {
  margin: {
    bottom: -.625rem;
    top: -.625rem;
  }
  .box {
    margin: .625rem 0;
    padding: 1.125rem;
  }
  a.box {
    @include hover-focus {
      p {
        color: $gray-dark;
      }
    }
  }
}
.box-header {
  background: $gray-dark;
  border-radius: $border-radius;
  h6 {
    color: $body-bg;
    font-size: .75rem;
    margin: 0;
    padding: {
      bottom: .75rem;
      top: .75rem;
    }
    + h6 {
      border-left: 1px solid #28323c;
    }
    a {
      border-bottom: 0.0625rem solid $abbr-border-color;
      color: inherit;
    }
  }
}
.box-divided {
  display: flex;
  padding: 0 .375rem;
  > * {
    border-left: .0625rem solid $gray-lighter;
    flex-basis: 1.5 / 7 * 100%;
    padding: 1rem 0.875rem;
    &:first-child {
      border-left: none;
      flex-basis: 4 / 7 * 100%;
    }
  }
}
a.box-divided {
  @include hover-focus {
    > * {
      border-left-color: $gray-lightish;
    }
  }
}
p, h2, .h2 {
  + .box,
  + .box-header {
    margin-top: 1rem;
  }
}

// Paged results
.paged-results {
  small, .small {
    color: #b2b6ba;
  }
}
.paged-results-header,
.paged-results-footer {
  align-items: baseline;
  display: flex;
  justify-content: space-between;
  > :last-child {
    text-align: right;
  }
}
.paged-results-header {
  margin-bottom: 1rem;
  h2 {
    margin: 0;
  }
}
.paged-results-footer {
  margin-top: 1.875rem;
  > :first-child,
  > :last-child {
    width: 50%;
  }
}
.pager {
  display: flex;
  flex-basis: 0;
  .btn {
    border: none;
    font-size: 1.125rem;
    line-height: 1;
    padding: 0.4375rem 0.6875rem;
    &:before {
      vertical-align: middle;
      line-height: .875rem;
      font-size: 1.6rem;
      width: .625rem;
    }
    + .btn {
      margin-left: 0.625rem;
    }
    &.disabled {
      background: #c8cacc;
      opacity: 1;
    }
  }
  // Tweak right arrow by 1 pixel.
  .ion-ios-arrow-forward:before {
    left: .0625rem;
    position: relative;
  }
}
.paged-results-none {
  hr {
    margin: 1rem 0 1.5rem;
  }
}

// Landing pages
.section-summary {
  padding: {
    bottom: 2.8125rem;
    top: 2.8125rem;
  }
  h2, .h2 {
    line-height: 1.33;
    margin: 1.5rem 0 .5rem;
  }
  p {
    margin-bottom: 1.5rem;
  }
  h2, .h2 {
    + .btn {
      margin-top: 0.625rem;
    }
  }
}
.news {
  background: $gray-lightest;
  padding: 2.8125rem 0;
  .box {
    background: $body-bg;
    p {
      margin-top: 1.1875rem;
    }
    h6 {
      @include multiline-ellipsis(1.38em, 2);
    }
  }
  header {
    img {
      margin-top: .75rem;
    }
    h2, .h2 {
      margin: 1rem 0 0;
      + .btn {
        margin-top: 1.125rem;
      }
    }
  }
}

// Member landing page
.member-landing-page {
  .section-summary {
    width: map-get($container-max-widths, sm) * .75;
  }
}

// Resource Library form
#category {
  min-width: 19.375rem;
  width: auto;
}

// Search
.search {
  .main-container {
    margin-bottom: 15rem;
  }
  form:last-child {
    margin-bottom: 0;
  }
}
.error-404 {
  .main-container {
    margin-bottom: 25rem;
  }
}

// Footer
.footer {
  background: $gray-lightest;
  &.footer-light {
    background: $body-bg;
  }
  font-size: .875em;
  p {
    color: #b2b6ba;
  }
  .nav {
    font-weight: 500;
  }
  .nav-link {
    color: $gray-light;
    padding: 0 0 0 $spacer * 2;
    @include hover-focus {
      color: $body-color;
    }
  }
}

.social-icons {
  margin-top: -6rem;
  position: fixed;
  right: 0;
  top: 50%;
  a {
    border-right: .25rem solid;
    color: #fff;
    font-size: 1.5rem;
    display: block;
    height: 3rem;
    left: .375rem;
    line-height: 3rem;
    position: relative;
    width: 3.25rem;
    text-align: center;
    @include transition(left .2s);
    &:hover {
      left: .125rem;
    }
  }
  .facebook {
    border-color: #3C5A94;
    background: {
      color: #3C5A94;
      position: center center;
      repeat: no-repeat;
      size: .6875rem 1.3125rem;
    }
  }
  .ion-logo-twitter {
    border-color: #1da1f2;
    background-color: #1da1f2;
  }
  .ion-logo-googleplus {
    border-color: #dd4b39;
    background-color: #dd4b39;
  }
  .ion-md-add {
    border-color: #ff6550;
    background-color: #ff6550;
    font-size: 1.75rem;
  }
}
